@use "global" as *;

.c-section-title__img {
  margin: 0 auto;
  width: rem(30);
  @include mq("md") {
    width: rem(40);
  }
}

.c-section-title__img img {
  width: 100%;
}

.c-section-title__heading {
  text-align: center;
  font-size: rem(20);
  line-height: 1;
  padding-top: rem(20);
  @include mq("md") {
    font-size: rem(36);
  }
}
